import {getSwiperData,getQuickData,getGoodsData,getRecommendData} from "../../../api/index"
export default {
    namespaced:true,
    state:{
        //轮播图变量
        SwiperData:[],
        //首页导航数据
        quickData:[],
        //首页产品核心
        goodsData:[],
        //首页推荐数据
        recommendData:[]
    },
    mutations:{
        //关联轮播图数据
        ["SET_SWIPERDATA"](state,payload){
            state.SwiperData = payload.SwiperData;
        },
        //关联首页导航数据
        ["SET_QUICKDATA"](state,payload){
            state.quickData = payload.quickData;
        },
        //关联首页产品核心数据
        ["SET_GOODSDATA"](state,payload){
            state.goodsData = payload.goodsData;
        },
        //关联首页推荐数据recommendData
        ["SET_RECOMMENDDATA"](state,payload){
            state.recommendData = payload.recommendData;
        }
    },
    actions:{
        //获取轮播图异步数据
        getSwiper(conText,payload){
            getSwiperData().then(res=>{
                if (res.code === 200){
                    conText.commit("SET_SWIPERDATA",{SwiperData:res.data});
                    if (payload.success){
                        payload.success();
                    }
                }
            })
        },
        //首页导航数据
        getQuick(conText,payload){
            getQuickData().then(res=>{
                if (res.code === 200){
                    conText.commit("SET_QUICKDATA",{quickData:res.data});
                    if (payload.success){
                        payload.success();
                    }
                }
            })
        },
        //首页产品核心数据
        getGoods(conText,payload){
            getGoodsData().then(res=>{
                if (res.code === 200){
                    conText.commit("SET_GOODSDATA",{goodsData:res.data});
                    if (payload.success){
                        payload.success();
                    }
                }
            })
        },
        //首页推荐数据
        getRecommend(conText,payload){
            getRecommendData().then(res=>{
                if (res.code === 200){
                    conText.commit("SET_RECOMMENDDATA",{recommendData:res.data});
                    if (payload.success){
                        payload.success();
                    }
                }
            })
        }
    }
}