import Vue from "vue"
import {getClassifyData,getClassifyContentData,getdetailsData,getReviewData,getspecsData,getAddFavData,getUserFavData,getDelUserFavData} from "../../../api/goods/index"
export default {
    namespaced:true,
    state:{
        //左边导航分类
        classifyData:[],
        //右边产品数据
        classifyContentData:[],
        //商品详情
        detailsData:[],
        //商品评价
        reviewdata:[],
        total:0,
        //商品规格
        specsdata:[],
        //用户收藏内容
        userFavs:[]
    },
    mutations:{
        //关联左边导航数据分类
        ["SET_CLASSIFYDATA"](state,payload){
            state.classifyData = payload.classifyData
        },
        //改变左边导航样式
        ["SELECT_ACTIVEITEM"](state,payload){
            if ( state.classifyData.length>0){
                for (let i = 0;i<state.classifyData.length;i++){
                    if (  state.classifyData[i].active){
                        state.classifyData[i].active = false;
                    }

                }
            }
            //改变样式为true
            state.classifyData[payload.index].active = true;
            //解决视图不渲染的问题
            Vue.set(state.classifyData,payload.index,state.classifyData[payload.index]);
        },
        //关联右边导航数据
        ["SET_CLASSIFTCONTENT"](state,payload){
            state.classifyContentData = payload.classifyContentData;
        },
        //商品详情
        ["SET_DETAILS"](state,payload){
            state.detailsData = payload.detailsData
        },
        //商品评价
        ["SET_REVIEW"](state,payload){
            state.reviewdata = payload.reviewdata;
            state.total = payload.total
        },
        //商品规格
        ["SET_SPECS"](state,payload){
            state.specsdata = payload.specsdata;
        },
        //商品规格样式改变
        ["SPECS_STYLE"](state,payload){
            if (state.specsdata.length>0){
                for (let i = 0;i<  state.specsdata.length;i++){
                    for (let j = 0; j<state.specsdata[payload.index].values.length;j++){
                       if ( state.specsdata[payload.index].values[j].active){
                           state.specsdata[payload.index].values[j].active = false
                       }
                    }
                }
            }
            state.specsdata[payload.index].values[payload.index2].active =true
            Vue.set( state.specsdata[payload.index].values[payload.index2],payload.index2,state.specsdata[payload.index].values[payload.index2])
        },
        //设置用户收藏内容
        ["SET_USERFAV"](state,payload){
            state.userFavs = payload.userFavs
        },
        //删除收藏
        ["DEL_USERFAV"](state,payload){
            state.userFavs.splice(payload.index,1)
        },
        //商品收藏分页
        ["SET_USERFAV_PAGE"](state,payload){
            state.userFavs.push(...payload.userFavs)
        }
    },
    actions:{
        //获取接口中的方法getClassifyData
        getClassify(conText,payload){
            getClassifyData().then(res=>{
                if (res.code === 200){
                    for (let i =0;i<res.data.length;i++) {
                        res.data[i].active = false;
                    }
                    conText.commit("SET_CLASSIFYDATA",{classifyData:res.data});
                    if (payload&&payload.success) {
                        payload.success()
                    }
                }
            })
        },
        //获取接口中的方法getClassifyContentData
        getClassifyContent(conText,payload){
            getClassifyContentData(payload.cid).then(res=>{
                if (res.code === 200){
                    conText.commit("SET_CLASSIFTCONTENT",{classifyContentData:res.data});
                    if (payload.success){
                        payload.success();
                    }
                }else {
                    conText.commit("SET_CLASSIFTCONTENT",{classifyContentData:""});
                }
            })
        },
        //商品详情
        getdetails(conText,payload){
            getdetailsData(payload.gid).then(res=>{
                if (res.code === 200){
                    conText.commit("SET_DETAILS",{detailsData:res.data})
                    if (payload&&payload.success){
                        payload.success()
                    }
                }
            })
        },
        //商品评价
        getReview(conText,payload){
            getReviewData(payload.gid).then(res=>{
                if (res.code === 200){

                    conText.commit("SET_REVIEW",{reviewdata:res.data,total:res.pageinfo.total})
                }else {
                    conText.commit("SET_REVIEW",{reviewdata:[],total:0})
                }
            })
        },
        //商品规格
        getspecs(conText,payload){
            getspecsData(payload.gid).then(res=>{
                if (res.code === 200){
                    for (let i = 0;i<res.data.length;i++){
                        for (let j = 0;j<res.data[i].values.length;j++){
                            res.data[i].values[j].active = false
                        }
                    }
                    conText.commit("SET_SPECS",{specsdata:res.data});
                }
            })
        },
        //商品加入收藏
        getAddFav(conText,payload){
            getAddFavData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                if (payload&&payload.success){
                    payload.success(res)
                }
            })
        },
        //用户收藏内容
        getUserFav(conText,payload){
            getUserFavData({uid:conText.rootState.user.uid,...payload}).then(res=>{
               if (res.code === 200){
                   conText.commit("SET_USERFAV",{userFavs:res.data})
               }else {
                   conText.commit("SET_USERFAV",{userFavs:[]})
               }
            })
        },
        //商品收藏分页
        getUserFavPage(conText,payload){
            getUserFavData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                let pageNum = 0;

                if (res.code === 200){
                    pageNum = res.pageinfo.pagenum;
                    conText.commit("SET_USERFAV_PAGE",{userFavs:res.data})
                }else {
                    pageNum = 0;
                    conText.commit("SET_USERFAV_PAGE",{userFavs:[]})
                }
                if (payload.success){
                    payload.success(pageNum)
                }
            })
        },
        //删除收藏
        getDelUserFav(conText,payload){
            getDelUserFavData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                if (res.code === 200){
                    conText.commit("DEL_USERFAV",{index:payload.index})
                }
            })
        }
    }
}