import {getAddAddressData,getAddressListData,getDelAddressListData,getAddressInfoData,getModAddressInfoData,getDefaultAddressData} from  "../../../api/address"
export default {
    namespaced:true,
    state:{
        //s收货地址列表
        addressList:[]
    },
    mutations:{
        //收货地址列表
        ["SET_ADDRESSLIST"](state,payload){
            state.addressList = payload.addressList
        },
        //删除收货地址
        ["DEL_ADDRESSLIST"](state,payload){
            state.addressList.splice(payload.index,1);
        }
    },
    actions:{
        //添加收货地址
        getAddAddress(conText,payload){
            getAddAddressData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                if (payload && payload.success){
                    payload.success(res)
                }
            })
        },
        //收货地址列表
        getAddressList(conText,payload){
            getAddressListData(conText.rootState.user.uid).then(res=>{
                if (res.code ===200) {
                    conText.commit("SET_ADDRESSLIST",{addressList:res.data})
                }
            })
        },
        //删除收货地址
        getDeladdressList(conText,payload){
           getDelAddressListData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                console.log(res)
                if (res.code === 200){
                    conText.commit("DEL_ADDRESSLIST",{index:payload.index})
                }

            });

        },
        //获取收货地址信息
        getAddressInfo(conText,payload){
            getAddressInfoData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                if (res.code === 200){
                    if (payload && payload.success){
                        payload.success(res)
                    }
                }
            })
        },
        //修改收货地址信息
        getModAddressInfo(conText,payload){
            getModAddressInfoData({uid:conText.rootState.user.uid,...payload}).then(res=>{
               if (res.code === 200){
                   if (payload.success){
                       payload.success()
                   }
               }
            })

        },
        //获取默认地址
        getDefaultAddress(conText,payload){
            getDefaultAddressData(conText.rootState.user.uid).then(res=>{
                if (res.code === 200){
                    if (payload.success){
                        payload.success()
                    }
                }
            })
        }

    }
}