import {hotKeywordsData,productSearchData,getAttributeData} from "../../../api/search/index"
import  Vue from "vue"
export default {
    namespaced:true,
    state:{
        //关键词数组
        keywordsData:localStorage["keywordsData"]?JSON.parse(localStorage['keywordsData']):[],
        //热门关键词
        hotkeywords:[],
        //产品搜索结果
        productData:[],
        //获取cid
        cid:"",
        //title
        title:"",
        //价格区间
        priceData:{
            items:[
                {price1:1,price2:50,active:false},
                {price1:51,price2:99,active:false},
                {price1:100,price2:300,active:false},
                {price1:301,price2:1000,active:false},
                {price1:1001,price2:4000,active:false},
                {price1:4001,price2:9999,active:false}
            ]
        },
        //最小价格
        minPrice:"",
        //最大价格
        maxPrice:"",
        //商品属性
        attrs:{},
        //设置商品属性
        params:[],
        //商品总件数
        total:0
    },
    mutations:{
        //关联关键词数组
        ["SET_KEYWORDS"](state,payload){
            state.keywordsData = payload.keywordsData;
            localStorage["keywordsData"] = JSON.stringify(state.keywordsData);
        },
        //清空关键词keywordsData
        ["CLEAR_KEYWORD"](state,payload){
            state.keywordsData = [];
            localStorage.removeItem("keywordsData");
        },
        //热门关键词关联
        ["SET_HOTKEYWORDS"](state,payload){
            state.hotkeywords = payload.hotkeywords;

        },
        //产品搜索结果关联
        ["SET_PRODUCT"](state,payload){
            state.productData = payload.productData;
            state.total = payload.total
        },
        ["SET_CID"](state,payload){
            state.cid = payload.cid

        },
        //设置价格排序
        ["SET_PRICESORT"](state,payload){
            if (state.priceData.items.length>0){
                for (let i = 0;i<state.priceData.items.length;i++){
                  if (i!==payload.index){
                      if (  state.priceData.items[i].active){
                          state.priceData.items[i].active = false;
                          break;
                      }
                  }
                }
                state.priceData.items[payload.index].active = !state.priceData.items[payload.index].active ;
                Vue.set(state.priceData.items,payload.index,state.priceData.items[payload.index]);
                state.minPrice = state.priceData.items[payload.index].active?state.priceData.items[payload.index].price1:"";
                state.maxPrice = state.priceData.items[payload.index].active?state.priceData.items[payload.index].price2:"";
            }
        },
        //设置最小价格
        ["MIN_PRICE"](state,payload){
            state.minPrice = payload.minPrice;
        },
        //设置最大价格
        ["MAX_PRICE"](state,payload){
            state.maxPrice = payload.maxPrice;
        },
        //商品属性
        ["SET_ATTRS"](state,payload){
            state.attrs = payload.attrs
        },
        //设置商品属性样式
        ["SET_ATTRS_STYLE"](state,payload){
            state.attrs[payload.index].param[payload.index1].active = !state.attrs[payload.index].param[payload.index1].active;
            Vue.set(state.attrs[payload.index].param,payload.index1,state.attrs[payload.index].param[payload.index1]);
        },
        //设置商品属性的pid
        ["SET_ATTRS_PID"](state){
            if(state.attrs.length>0){
                state.params=[];
                for(let i=0;i<state.attrs.length;i++){
                    for(let j=0;j<state.attrs[i].param.length;j++){
                        if(state.attrs[i].param[j].active){
                            state.params.push(state.attrs[i].param[j].pid);
                        }
                    }
                }
            }
        },
        //商品属性重置
        ["SET_RESET"](state){
            state.cid = "";
            //价格重置
            if (state.priceData.items.length>0){
                for (let i = 0;i<state.priceData.items.length;i++){

                        if (  state.priceData.items[i].active){
                            state.priceData.items[i].active = false;
                            break;
                        }
                }
            }
            state.minPrice = "";
            state.maxPrice= "";

            //商品属性
            //重置属性
            if(state.attrs.length>0){
                for(let i=0;i<state.attrs.length;i++){
                    for(let j=0;j<state.attrs[i].param.length;j++){
                        if(state.attrs[i].param[j].active){
                            state.attrs[i].param[j].active=false;
                        }
                    }
                }
                state.params=[];
            }
        },
        //增加分页数据
        ["SET_SEARCH_DATA_PAGE"](state,payload){
            if(payload.productData.length>0){
                for(let i=0;i<payload.productData.length;i++){
                    state.productData.push(payload.productData[i]);
                }
            }
        },

    },
    actions:{
        //热门搜索关键词
        hotKeywords(conText){
            hotKeywordsData().then(res=>{
               if (res.code === 200){
                   conText.commit("SET_HOTKEYWORDS",{hotkeywords:res.data})
               }
            })
        },
        //选择分类
        selectClassify(conText,payload){
            if ( conText.rootState.goods.classifyData.length>0){
                for (let i = 0;i<conText.rootState.goods.classifyData.length;i++){
                    if (i!==payload.index){
                        if ( conText.rootState.goods.classifyData[i].active){
                            conText.rootState.goods.classifyData[i].active =false;
                        }
                    }

                }
                conText.rootState.goods.classifyData[payload.index].active = !conText.rootState.goods.classifyData[payload.index].active;
                Vue.set( conText.rootState.goods.classifyData,payload.index,conText.rootState.goods.classifyData[payload.index]);
                let cid = conText.rootState.goods.classifyData[payload.index].active?conText.rootState.goods.classifyData[payload.index].cid:"";
                conText.commit("SET_CID",{cid:cid})
            }


        },

        //产品搜索结果
        productSearch(conText,payload){
            productSearchData(payload).then(res=>{
                let pageNum=0;
               if (res.code === 200){
                   pageNum=res.pageinfo.pagenum;
                   conText.commit("SET_PRODUCT",{productData:res.data,total:parseInt(res.pageinfo.total)})

               }else {
                   pageNum=0;
                   conText.commit("SET_PRODUCT",{productData:[],total:0})
               }
                if(payload.success){
                    payload.success(pageNum);
                }

            })
        }
        ,
        //商品分页
        getSearchPage(conText,payload){
            productSearchData(payload).then(res=>{
                if(res.code===200){
                    conText.commit("SET_SEARCH_DATA_PAGE",{productData:res.data});
                }
            })
        },
        //商品属性
        getAttribute(conText,payload){
            getAttributeData(payload.kwords).then(res=>{
                //console.log(res)
                if (res.code === 200){
                    for (let i= 0;i<res.data.length;i++){
                        for (let j = 0;j<res.data[i].param.length;j++){
                            res.data[i].param[j].active =false
                        }
                    }
                    conText.commit("SET_ATTRS",{attrs:res.data})

                }else {
                    conText.commit("SET_ATTRS",{attrs:[]})
                }

            })
        },
        //商品属性重置
        getAttributeReset(conText,payload){
            if ( conText.rootState.goods.classifyData.length>0){
                for (let i = 0;i<conText.rootState.goods.classifyData.length;i++){
                    if (i!==payload.index){
                        if ( conText.rootState.goods.classifyData[i].active){
                            conText.rootState.goods.classifyData[i].active =false;
                        }
                    }

                }


                conText.commit("SET_RESET")
            }
        }

    }
}