import  {getOrderData,getLastorderNumData,myOrderData,clearOrderData,sureOrderData,getOrderDetailsData,getReviewOrderData
,getOrderServersData,getHandReviewData} from  "../../../api/order"
export default  {
    namespaced:true,
    state:{
        //我的订单
        orders:[],
        //订单详情
        orderInfo:{},
        //待评价订单
        revieworders:[],
        //评价项目选项
        orderservers:[]
    },
    mutations:{
        //我的订单
        ["SET_ORDERS"](state,payload){
            state.orders = payload.orders
        },
        //订单分页
        ["SET_ORDERS_PAGE"](state,payload){
            state.orders.push(...payload.orders)
        },
        //取消订单
        ["DEL_ORDERS"](state,payload){
            state.orders.splice(payload.index,1)
        },
        //改变订单状态
        ["SET_STATUS"](state,payload){
            state.orders[payload.index].status = payload.status
        },
        //订单详情
        ["SET_ORDER_DETAILS"](state,payload){
           state.orderInfo = payload.orderInfo;
        },
        //待评价订单
        ["SET_REVIEWORDER"](state,payload){
            state.revieworders = payload.revieworders;
        },
        //设置评价项目选项
        ["SET_ORDERSERVER"](state,payload){
            state.orderservers = payload.orderservers;
        },
        //待评价分页功能
        ["SET_REVIEWORDER_PAGE"](state,payload){
            state.revieworders.push(...payload.revieworders)
        },
        //设置评论项目分数
        ["SET_ORDERSERVER_NUMBER"](state,payload){
            if (state.orderservers.length>0){
                for(let i=payload.index2+1;i<state.orderservers[payload.index].scores.length;i++){
                    state.orderservers[payload.index].scores[i].active=false;
                }
                for (let i =0;i<payload.index2;i++){
                    state.orderservers[payload.index].scores[i].active =true
                }
                state.orderservers[payload.index].score = payload.score;
                console.log( state.orderservers[payload.index].score)
            }

        }
    },
    actions:{
        //提交商品订单
        getOrder(conText,payload){
            return getOrderData({uid:conText.rootState.user.uid,...payload}).then(res=>{
               return res
            })
        },
        //获取订单编号
        getLastorderNum(conText,payload){
             getLastorderNumData(conText.rootState.user.uid).then(res=>{
                if (payload.success){
                    payload.success(res)
                }

            })
        },
        //我的订单
        myOrder(conText,payload){
            myOrderData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                let pageNum = 0;
                console.log(res);
               if (res.code === 200){
                   pageNum = res.pageinfo.pagenum;
                   conText.commit("SET_ORDERS",{orders:res.data})
               }else {
                   pageNum=0;
                   conText.commit("SET_ORDERS",{orders:[]})
               }
               if (payload.success){
                   payload.success(pageNum)
               }
            })
        },
        //订单分页
        myOrderPage(conText,payload){
            myOrderData({uid:conText.rootState.user.uid,...payload}).then(res=>{

                    conText.commit("SET_ORDERS_PAGE",{orders:res.data})
            })
        },
        //取消订单
        clearOrder(conText,payload){
            clearOrderData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                if (res.code === 200){
                    conText.commit("DEL_ORDERS",{index:payload.index})
                }
            })
        },
        //确定订单
        sureOrder(conText,payload){
            sureOrderData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                console.log(res)
                if (res.code === 200){
                    conText.commit("SET_STATUS",{index:payload.index,status:payload.status})
                }
            })
        },
        //会员订单详情
        getOrderDetails(conText,payload){
            getOrderDetailsData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                if (res.code === 200){
                    conText.commit("SET_ORDER_DETAILS",{orderInfo:res.data})
                }
            })
        },
        //待评价订单
        getReviewOrder(conText,payload){
            getReviewOrderData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                let pageNum = 0;
                if (res.code ===200){
                    pageNum=res.pageinfo.pagenum;
                    conText.commit("SET_REVIEWORDER",{revieworders:res.data})
                }else {
                    pageNum=0;
                    conText.commit("SET_REVIEWORDER",{revieworders:[]})
                }
                if (payload.success){
                    payload.success(pageNum)
                }
            })
        },
        //待评价订单分页
        getReviewOrderPage(conText,payload){
            getReviewOrderData({uid:conText.rootState.user.uid,...payload}).then(res=>{

                    conText.commit("SET_REVIEWORDER_PAGE",{revieworders:res.data})
            })
        },
        //评价服务项目
        getOrderServers(conText,payload){
            getOrderServersData().then(res=>{
               if (res.code === 200){
                   for (let i = 0;i<res.data.length;i++){
                        res.data[i].score = 5;
                       res.data[i].scores = [
                           {
                               value:1,
                               active:true
                           },
                           {
                               value:2,
                               active:true
                           },
                           {
                               value:3,
                               active:true
                           },
                           {
                               value:4,
                               active:true
                           },
                           {
                               value:5,
                               active:true
                           }
                       ]
                   }
                   console.log(res)
                   conText.commit("SET_ORDERSERVER",{orderservers:res.data})
               }
            })
        },
        //提交评价
        getHandReview(conText,payload){
            getHandReviewData({uid:conText.rootState.user.uid,...payload}).then(res=>{
                if (res.code === 200){
                    if (payload.success){
                        payload.success()
                    }
                }
            })

        }
    }
}