import Vue from "vue"

export default {
    namespaced: true,
    state: {
        //购物车数据
        goodsDatas: localStorage["goodsDatas"] ? JSON.parse(localStorage["goodsDatas"]) : []
    },
    mutations: {
        //购物车数据
        ["SET_GOODS_DATA"](state, payload) {
            let isSame = false;
            if (state.goodsDatas.length > 0) {
                for (let i = 0; i < state.goodsDatas.length; i++) {
                    if (state.goodsDatas[i].gid === payload.goodsDatas.gid
                        && JSON.stringify(state.goodsDatas[i].attrs) === JSON.stringify(payload.goodsDatas.attrs)
                        && JSON.stringify(state.goodsDatas[i].attrs.param) === JSON.stringify(payload.goodsDatas.attrs.param)
                    ) {
                        isSame = true;
                        state.goodsDatas[i].amount = parseInt(state.goodsDatas[i].amount) + parseInt(payload.goodsDatas.amount);
                        break;
                    }
                }
            }
            if (!isSame) {
                state.goodsDatas.push(payload.goodsDatas);
            }
            localStorage["goodsDatas"] = JSON.stringify(state.goodsDatas);
        },
        //删除商品
        ["DEL_GOODSDATAS"](state, payload) {
            if (state.goodsDatas.length > 0) {
                state.goodsDatas.splice(payload.index, 1)
            }
            localStorage["goodsDatas"] = JSON.stringify(state.goodsDatas);
        },
        //增加商品数量
        ["ADD_GOODSDATAS"](state, payload) {
            state.goodsDatas[payload.index].amount += 1;
            Vue.set(state.goodsDatas, payload.index, state.goodsDatas[payload.index]);
            localStorage["goodsDatas"] = JSON.stringify(state.goodsDatas);
        },
        //减少商品数量
        ["DEC_GOODSDATAS"](state, payload) {
            state.goodsDatas[payload.index].amount = state.goodsDatas[payload.index].amount > 1 ? --state.goodsDatas[payload.index].amount : 1;
            Vue.set(state.goodsDatas, payload.index, state.goodsDatas[payload.index]);
            localStorage["goodsDatas"] = JSON.stringify(state.goodsDatas);
        },
        //选择商品
        ["SELECT_GOODSDATAS"](state,payload){
            state.goodsDatas[payload.index].checked = ! state.goodsDatas[payload.index].checked;
            Vue.set(state.goodsDatas, payload.index, state.goodsDatas[payload.index]);
            localStorage["goodsDatas"] = JSON.stringify(state.goodsDatas);
        },
        //更改商品数量
        ["CHANGE_GOODSDATAS"](state,payload){
            state.goodsDatas[payload.index].amount = parseInt(payload.amount);
            if (! state.goodsDatas[payload.index].amount){
                state.goodsDatas[payload.index].amount =1

            }
            Vue.set(state.goodsDatas, payload.index, state.goodsDatas[payload.index]);
            localStorage["goodsDatas"] = JSON.stringify(state.goodsDatas);
        },
        //全选/反选
        ["ALL_CHANGE_GOODSDATAS"](state,payload){
            if (state.goodsDatas.length > 0) {
                for (let i = 0; i < state.goodsDatas.length; i++) {

                       state.goodsDatas[i].checked = payload.checked;


                }
            }
            localStorage["goodsDatas"] = JSON.stringify(state.goodsDatas);
        },


    },
    getters: {
        //计算价格总数
        total(state) {
            if (state.goodsDatas.length > 0) {
                let total = 0;
                for (let i = 0; i < state.goodsDatas.length; i++) {
                    if (state.goodsDatas[i].checked){
                        total += state.goodsDatas[i].price*state.goodsDatas[i].amount ;
                }

                }
                return parseFloat(total.toFixed(2))
            } else {
                return 0;
            }
        },
        //运费
        freight(state) {
            if (state.goodsDatas.length > 0) {
                let freight = [];
                for (let i = 0; i < state.goodsDatas.length; i++) {
                    if (state.goodsDatas[i].checked ) {
                        freight.push(state.goodsDatas[i].freight);
                    }
                }
                return  freight.length>0?Math.max.apply(null, freight):0;
            } else {
                return 0;
            }
        }
    }
}