import  {getLoginData,getVcodeData,getRegisterData,getUserRegisterData,
    getOutloginData,getUserInfoData,getProtectUserData,uploadHeadData,getUpdateUserData,
    updateCellphoneData,getModPasswordData} from "../../../api/user"
export default {
    namespaced:true,
    state:{
        uid:localStorage["uid"]?localStorage["uid"]:"",
        nickname:localStorage["nickname"] ?localStorage["nickname"] :"",
        isLogin:localStorage["isLogin"]?Boolean(localStorage["isLogin"]):false,
        auth_token:localStorage["auth_token"]?localStorage["auth_token"]:"",
        head:"",
        points:""
    },
    mutations:{
        //会员登录
        ["SET_LOGIN"](state,payload) {
            state.uid = payload.uid;
            state.nickname = payload.nickname;
            state.isLogin = payload.isLogin;
            state.auth_token = payload.auth_token;
            localStorage["uid"] = payload.uid;
            localStorage["nickname"] = payload.nickname;
            localStorage["isLogin"] = payload.isLogin;
            localStorage["auth_token"] = payload.auth_token;
        },
        //安全退出
        ["OUT_LOGIN"](state){
            state.uid = "";
            state.nickname = "";
            state.isLogin = false;
            state.auth_token = "";
            localStorage.removeItem("uid");
            localStorage.removeItem("nickname");
            localStorage.removeItem("isLogin");
            localStorage.removeItem("auth_token");
        },
        //获取会员信息
        ["SET_USERINFO"](state,payload){
            state.head = payload.head;
            state.points = payload.points;
            state.nickname = payload.nickname;
        }
    },
    actions:{
        //会员登录
        getLogin(conText,payload){
            return getLoginData(payload).then(res=>{
                conText.commit("SET_LOGIN",{uid:res.data.uid,nickname:res.data.nickname,isLogin:true,auth_token:res.data.auth_token});
                return res
            })
        },
        //检验验证码是否正确
        getVcode(conText,payload){
            return getVcodeData(payload.vcode).then(res=>{
                return res;
            })
        },
        //检测是否注册过手机号码
        getRegister(conText,payload){
            return getRegisterData(payload.username).then(res=>{
                return res
            })
        },
        //会员注册
        getUserRegister(conText,payload){
            return getUserRegisterData(payload).then(res=>{
                return res
            })
        },
        //安全退出
        getOutlogin(conText){
            getOutloginData({uid:conText.state.uid}).then(res=>{
            });
            conText.commit("OUT_LOGIN");
        },
        //获取会员信息
        getUserInfo(conText,payload){
            getUserInfoData(conText.state.uid).then(res=>{
                if (res.code === 200){
                    conText.commit("SET_USERINFO",{head:res.data.head,points:res.data.points,nickname:res.data.nickname})

                }
                if (payload && payload.success){
                    payload.success(res)
                }
            })
        },
        //会员安全认证
        getProtectUser(conText,payload){
            getProtectUserData({uid:conText.state.uid,auth_token:conText.state.auth_token}).then(res=>{
                console.log(res)
                if (res.code!==200){
                    conText.commit("OUT_LOGIN");
                }

                if (payload && payload.success){
                    payload.success(res)
                }
            })
        },
        //头像上传
        uploadHead(conText,payload){
            uploadHeadData(payload).then(res=>{
                if (payload && payload.success){
                    payload.success(res)
                }
            })
        },
        //修改会员信息
        getUpdateUser(conText,payload){
            getUpdateUserData({uid:conText.state.uid,auth_token:conText.state.auth_token,...payload}).then(res=>{
                if (payload && payload.success){
                    payload.success(res)
                }
            })
        },
        //修改手机号码
        updateCellphone(conText,payload){
            updateCellphoneData({uid:conText.state.uid,...payload}).then(res=>{
                if (payload && payload.success){
                    payload.success(res)
                }
            })
        },
        //修改密码
        getModPassword(conText,payload){
            getModPasswordData({uid:conText.state.uid,...payload}).then(res=>{
                if (payload && payload.success){
                    payload.success(res)
                }
            })
        }
    }
}