import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);


const routes = [

    {
        path: '/',
        name: "main",
        component: () => import("../views/main"),
        redirect: "/index",
        meta:{keepAlive:true},
        children: [
            {
                path: 'index',
                name: 'index',
                component: () => import("../views/index"),
                meta: {keepAlive:true,title: "商城"}
            },
            {
                path: 'cart',
                name: 'cart',
                component: () => import("../views/cart"),
                meta: {keepAlive:false,title: "购物车"}
            },
            {
                path: 'pcenter',
                name: 'pcenter',
                component: () => import("../views/pcenter"),
                meta: {keepAlive:false,title: "个人中心"}
            }
        ]

    },
    {
        path:'/goods/classify',
        name:'goods-classify',
        component:()=>import("../views/goods/classify"),
        meta:{title:"商品分类"},
        redirect:"/goods/classify/item",
        children:[
            {
                path:'item',
                name:"goods-classify-item",
                component:()=>import("../views/goods/classify_item"),
                meta:{title:"商品分类"}
            }
        ]
    },
    {
        path:"/goods/search",
        name:"goods-search",
        component:()=>import("../views/goods/search"),
        meta:{title:"搜索页面"}
    },
    {
        path:"/goods/search/content",
        name:"goods-search-content",
        component:()=>import("../views/goods/search_content"),
        meta:{title:"搜索内容页面"}
    },
    {
        path:"/login",
        name:"login",
        component:()=>import("../views/login"),
        meta:{title:"登录页面"}
    },
    {
        path:"/register",
        name:"register",
        component:()=>import("../views/register"),
        meta:{title:"注册页面"}
    },
    {
        path:"/goods/details",
        name:"goods-details",
        component:()=>import("../views/goods/details"),
        redirect:"/goods/details/item",
        meta:{title:"商品详情页面"},
        children:[
            {
                path:'item',
                name:"goods-item",
                component:()=>import("../views/goods/details_item"),
            },
            {
                path:'content',
                name:"goods-content",
                component:()=>import("../views/goods/details_content"),
            },
            {
                path:'review',
                name:"goods-review",
                component:()=>import("../views/goods/details_review"),
            },

        ]
    },
    {
        path:'/order',
        name:"order",
        component:()=>import("../views/order"),
        meta:{title:"订单页面",auth:true}
    },
    {
        path:'/address/add',
        name:"address-add",
        component:()=>import("../views/address/add"),
        meta:{title:"添加收货地址管理",auth:true}
    },
    {
        path:'/address',
        name:"address",
        component:()=>import("../views/address"),
        meta:{title:"选择收货地址",auth:true}
    },
    {
        path:'/address/mod',
        name:"address-mod",
        component:()=>import("../views/address/mod"),
        meta:{title:"修改收货地址",auth:true}
    },
    {
        path:'/order/end',
        name:"order-end",
        component:()=>import("../views/order/end"),
        meta:{title:"下单成功",auth:true}
    },
    {
        path:'/user/order',
        name:"user-order",
        component:()=>import("../views/user/order/index"),
        meta:{title:"全部订单",auth:true},
        redirect:"/user/order/list",
        children:[
            {
                path:"list",
                name:"user-order-list",
                component:()=>import("../views/user/order/list"),
                meta:{title:"全部订单",auth:true}
            },
            {
                path:"review",
                name:"user-order-review",
                component:()=>import("../views/user/order/review"),
                meta:{title:"待评价",auth:true}
            }
        ]
    },
    {
        path:'/user/order/details',
        name:"user-order-details",
        component:()=>import("../views/user/order/details"),
        meta:{title:"订单详情",auth:true}
    },
    {
        path:'/user/order/add_review',
        name:"user-order-add_review",
        component:()=>import("../views/user/order/add_review"),
        meta:{title:"评价页面",auth:true}
    },
    {
        path:'/user/profile',
        name:"user-profile",
        component:()=>import("../views/user/profile"),
        meta:{title:"个人资料",auth:true}
    },
    {
        path:'/user/bind_cellphone',
        name:"user-bind_cellphone",
        component:()=>import("../views/user/bind_cellphone"),
        meta:{title:"绑定手机",auth:true}
    },
    {
        path:'/user/mod_password',
        name:"user-mod_password",
        component:()=>import("../views/user/mod_password"),
        meta:{title:"修改密码",auth:true}
    },
    {
        path:'/user/fav',
        name:"user-fav",
        component:()=>import("../views/user/fav"),
        meta:{title:"我的收藏",auth:true}
    }

];

const router = new VueRouter({
    mode: "history",//1、hash哈希：有#号。2、history历史：没有#号
    base: process.env.BASE_URL, //自动获取根目录路径
    scrollBehavior:(to,from,position)=>{
        if(position){
            return position
        }else{
            return {x:0,y:0}
        }
    },
    routes
});
router.beforeEach((to,from,next)=>{
    if (to.meta.auth){
        if (Boolean(localStorage['isLogin'])){
            next();
        } else {
            next("/login");
        }
    } else {
        next();
    }
});
export default router
